import React from 'react';
import {Panel, PanelHeader, Group, Cell, List, Header, Search, Button, Avatar} from '@vkontakte/vkui';
import eruda from 'eruda';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Add from '@vkontakte/icons/dist/24/add';
  
const Home = ({id, this: props}) => (
    <Panel id={id}>
        <PanelHeader left={<Header onClick={() => props.setState({activePanel: 'category', history: [...props.state.history, 'category']})}><Icon24List/></Header>}>Рецепты</PanelHeader>
       <Search maxLength="30" value={props.state.search} onChange={(e) => props.onChange(e)}/>
       <Cell
        component="a"
        target="_blank"
        href="https://vk.com/covid19"
        size="l"
        before={<Avatar style={{backgroundImage: "url('data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2064%2064%22%3E%3Cpath%20fill%3D%22%234bb34b%22%20d%3D%22M0%200h64v64H0z%22%2F%3E%3Cpath%20d%3D%22M17.21%2017l12.12-4.07a11.49%2011.49%200%20012-.55%205%205%200%20011.34%200%2011.49%2011.49%200%20012%20.55L46.79%2017A1.78%201.78%200%200148%2018.64v16.59q0%2012-14.6%2018.2a3.58%203.58%200%2001-2.8%200Q16%2047.19%2016%2035.23V18.64A1.78%201.78%200%200117.21%2017z%22%20fill-rule%3D%22evenodd%22%20fill%3D%22%23fff%22%2F%3E%3Cpath%20d%3D%22M32.72%2024.34a1%201%200%2000-1.45%200l-7.66%208c-.34.35-.22.64.27.64H26v5.55a.44.44%200%2000.44.45h3.12a.44.44%200%2000.44-.44v-2.76A1.78%201.78%200%200131.78%2034h.44A1.78%201.78%200%200134%2035.78v2.78a.44.44%200%2000.44.44h3.12a.45.45%200%2000.44-.45V33h2.12c.48%200%20.6-.29.27-.64z%22%20fill-rule%3D%22evenodd%22%20fill%3D%22%234bb34b%22%2F%3E%3C%2Fsvg%3E')", marginTop: "5px", marginLeft: "5px", }} size={55}/>}
      >
          <div style={{marginTop: "10px", fontWeight: "900"}}>Главное о Covid-19</div>
          <div style={{color: "gray", whiteSpace: "pre-wrap"}}>#stayhome</div>
      </Cell>
        {props.state.subscriber ? null:
        <Group>
        <Cell
          description="Группа сервиса"
          bottomContent={<Button before={<Icon24Add/>} onClick={() => props.Subscribe()}>Вступить</Button>}
          before={<Avatar src="https://sun9-3.userapi.com/c855428/v855428157/1874e1/TCZ8G7ZYv8o.jpg"/>}
          size="l"
        >
          Xelene Studio
        </Cell>
      </Group>
       }
        <Group>
            <List>
                {props.recipes.length > 0 &&
                    props.recipes.map(function(result, i) {
                        return <Cell expandable key={i} onClick={() => {props.GetRecipesInfo(result['id'])}}>{result['name']}</Cell>
                    })
                }
                {props.recipes.length === 0 &&
                    <Cell style={{userSelect: "none"}} onClick={() => eruda.init()}>
                        По вашему запросу ничего не найдено</Cell>
                }
            </List>
        </Group>
    </Panel>
);


export default Home;