import React from 'react'; // Основа
import connect from '@vkontakte/vk-connect'; // VK Connect
import {View, ScreenSpinner, Alert, Epic, Tabbar, TabbarItem, ConfigProvider, Div, Button, ModalPage, ModalPageHeader, ModalRoot, HeaderButton, Input, FormLayoutGroup, FormLayout, Textarea, FormStatus} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from "./panels/home";
import Recipes_info from "./panels/recipes_info";
import Category from "./panels/category";
import CategoryADV from './panels/category_advance';
import Recipe_Suggest from './panels/recipe_suggest.js';
import More from './panels/More'
import Add from './panels/add'
import Add_Recipe from './panels/add_recipe'

import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28Menu from '@vkontakte/icons/dist/28/menu';
import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';
const queryString = require('query-string');
const parsedHash = queryString.parse(window.location.hash);
const h = [];
class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // Panel
            activePanel: 'home',
            activeStory: 'home',
            popout: <ScreenSpinner/>,
            sheme: '',
            // App Info
            api_url: "https://api.xelene.me/recipes/?",
            recipes: [],
            suggested: [], // Предложка
            search: '',
            add_category: '',
            add_author_name: '',
            error_moderation: '',
            add_time_for_preparing: '',
            add_ingredients: '',
            add_recipe: '',
            search_category: '',
            add_name: "",
            recipes_info_full: {"information": {}},
            category: [],
            category_selected: '',
            tema: null,
            history: ['home'],
            // For Debug and tem
            debug: false,
            // Подписота
            subscriber: true
        };
        this.onChangeRezeptAddName = this.onChangeRezeptAddName.bind(this);
        this.onChangeRezeptAddCategory = this.onChangeRezeptAddCategory.bind(this);
        this.onChangeRezeptAddAuthorName = this.onChangeRezeptAddAuthorName.bind(this);
        this.onChangeRezeptAddTime = this.onChangeRezeptAddTime.bind(this);
        this.onChangeRezeptAddIngred = this.onChangeRezeptAddIngred.bind(this);
        this.onChangeRezeptAddRecipe = this.onChangeRezeptAddRecipe.bind(this);
        this.Share = this.Share.bind(this);
        this.closePopout = this.closePopout.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onChangeCategory = this.onChangeCategory.bind(this);
        this.SuggestRecipe = this.SuggestRecipe.bind( this );
        this.onStoryChange = this.onStoryChange.bind(this);
        
    }
    
    goBack = () => {
        const history = [...this.state.history];
        history.pop();
        const activePanel = history[history.length - 1];
        if (activePanel === 'home') {
          connect.send('VKWebAppDisableSwipeBack');
        }
        this.setState({ history, activePanel });
      }
    
      goForward = (activePanel) => {
        const history = [...this.state.history];
        history.push(activePanel);
        if (this.state.activePanel === 'home') {
          connect.send('VKWebAppEnableSwipeBack');
        }
        this.setState({ history, activePanel });
      }

    componentDidMount() {
        let self = this;
        window.addEventListener('popstate', function (event) {
            if(h.length > 0) {
                self.goToBack(h[h.length - 1])
            }
        }, false);

        if(parsedHash.recipe === undefined) {
        // Подпись
        fetch(this.state.api_url + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                if(result.response) { // Успешно
                    this.setState({activePanel: 'home', popout: null});
                    this.GetRecipes();
                }
                else {
                    this.setState({
                        popout:
                            <Alert actions={[{title: 'Взломать Дурова', autoclose: true, style: 'default'}]} onClose={this.closePopout}>
                                <h2>Запрос заблокирован!</h2>
                                <p>Не удалось проверить подпись.</p>
                            </Alert>
                    })
                }
            })
            .catch(async err => {
                this.setState({activePanel: 'failed-loading', popout: null});
            })
        }else {
            fetch(this.state.api_url + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                if(result.response) { // Успешно
                    this.GetRecipes();
                }
                else {
                    this.setState({
                        popout:
                            <Alert actions={[{title: 'Взломать Дурова', autoclose: true, style: 'default'}]} onClose={this.closePopout}>
                                <h2>Запрос заблокирован!</h2>
                                <p>Не удалось проверить подпись.</p>
                            </Alert>
                    })
                }
            })
            .catch(async err => {
                this.setState({activePanel: 'failed-loading', popout: null});
            })
            this.GetRecipesInfo(parsedHash.recipe)
        }
        connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
                document.body.attributes.setNamedItem(schemeAttribute);
                this.setState({sheme: data.scheme})
                if(data.scheme === "space_gray") {
                    connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light"});
                } else {
                    connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark"});
                }
			}
		});
    }
    closePopout () {
        this.setState({ popout: null });
    }
    onChange(e) {
        this.setState({ search: e.target.value });
    }
    

    onChangeCategory(search_category) {
        this.setState({ search_category });
    }

    onChangeRezeptAddName(add_name) {
        this.setState({ add_name: add_name.target.value});
    }

    onChangeRezeptAddCategory(add_category) {
        this.setState({add_category: add_category.target.value })
    }
    onChangeRezeptAddAuthorName(add_author_name) {
        this.setState({ add_author_name: add_author_name.target.value})
    }

    onChangeRezeptAddTime(add_time_for_preparing) {
        this.setState({add_time_for_preparing: add_time_for_preparing.target.value})
    }

    onChangeRezeptAddIngred(add_ingredients) {
        this.setState({add_ingredients: add_ingredients.target.value})
    }

    onChangeRezeptAddRecipe(add_recipe) {
        this.setState({add_recipe: add_recipe.target.value})
    }

    GetRecipesInfo(id) {
        this.setState({popout: <ScreenSpinner/>});
        fetch(this.state.api_url + "method=get.recipes&id=" + id +  "&" + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                if(result.response) {
                    this.setState({
                        recipes_info_full: result.response,
                        popout: null,
                        activePanel: 'recipe_info',
                        history: [...this.state.history, 'recipe_info']});
                }
            })
            .catch(async err => {
                this.setState({activePanel: 'failed-loading', popout: null});
            })
    }
    GetRecipes() {
        this.setState({popout: <ScreenSpinner/>});
        fetch(this.state.api_url + "&method=is.member&" + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                this.setState({subscriber: result.response})
            })
            .catch(err => {
                this.setState({activePanel: "failed-loading", popout:null})
            });
        fetch(this.state.api_url + "method=get.recipes&" + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                this.setState({recipes: result.response});
                this.setState({popout: null});
                let category_list = [];
                for(let i = 0; i < result.response.length; i++) {
                    if(category_list.indexOf(result.response[i]['category']) === -1) {
                        category_list.push(result.response[i]['category'])
                    }
                }
                this.setState({category: category_list})
            })
            .catch(async err => {
                this.setState({activePanel: 'failed-loading', popout: null});
            })
    }

    GetSuggested() {
        this.setState({popout: <ScreenSpinner/>});

        fetch(this.state.api_url + "method=get.suggested&" + window.location.search.replace('?', ''))
            .then(res => res.json())
            .then(result => {
                this.setState({suggested: result.response.items });
                this.setState({popout: null});
            })
            .catch(async err => {
                this.setState({error: '', popout: null});
            })
    }


    get recipes () {
        const search = this.state.search.toLowerCase();
        return this.state.recipes.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    get category() {
        const search = this.state.search_category.toLowerCase();
        return this.state.category.filter(a => a.toLowerCase().indexOf(search) > -1);
    }


    UpdateTheme() {
        if(this.state.sheme === "bright_light") {
            this.setState({sheme: 'space_gray'});
            document.body.setAttribute('scheme', "space_gray");
            connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light"});
        }
        else if(this.state.sheme === "space_gray"){
            this.setState({sheme: 'bright_light'});
            document.body.setAttribute('scheme', "bright_light");
            connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark"});
        }
        else if(this.state.sheme === "client_light") {
            this.setState({sheme: 'space_gray'});
            document.body.setAttribute('scheme', "space_gray");
            connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light"});
        }
        else if(this.state.sheme === "client_dark"){
            this.setState({sheme: 'bright_light'});
            document.body.setAttribute('scheme', "bright_light");
            connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark"});
        }
    }

    SuggestRecipe() {
        let recipe = {
            name: this.state.add_name,
            category: this.state.add_category,
            author_name: this.state.add_author_name,
            time_for_preparing: this.state.add_time_for_preparing,
            ingredients: this.state.add_ingredients,
            recipe: this.state.add_recipe
        };

        var query = '&';

        Object.keys( recipe ).forEach( key => {
            query += key + '=' + recipe[key] + '&';
        } );

        this.setState({popout: <ScreenSpinner/>});
        fetch( this.state.api_url + "method=send.receipt&" + window.location.search.replace('?', '') + query )
            .then(res => res.json())
            .then(result => {
                if( result.response ) {
                    this.setState({
                    add_name: '',
                    add_category: '',
                    add_author_name: '',
                    add_time_for_preparing: '',
                    add_ingredients: '',
                    add_recipe: '',
                    popout: null
                });
                    this.GetSuggested();
                    this.goBack()
                } else {
                    this.setState({popout: 
                        <Alert actions={[{title: 'Закрыть', autoclose: true, style: 'default'}]} onClose={this.closePopout}>
                        <h2>Ошибка!</h2>
                    <p>{result.error.message}</p>
                    </Alert>
                    })
                }
            })
    }

    Subscribe() {
        this.setState({subscriber: true});
        connect.send("VKWebAppJoinGroup", {"group_id": 183054783});
    }

    Share() {
        connect.send("VKWebAppShowWallPostBox", {"message": "Нашел рецепт " +this.state.recipes_info_full['name']+ "\nИнгредиенты:\n " +this.state.recipes_info_full['information']['ingredients']+ "\nСам рецепт: \n" +this.state.recipes_info_full['information']['recipe']+ "\n\nЗаглянуть в рецепт - https://vk.com/app7022864#recipe=" +this.state.recipes_info_full['id']});
    }
    onStoryChange (e) {
        if(e.currentTarget.dataset.story === "add") {
            this.GetSuggested()
            this.setState({history: ["add"]})
        } else if(e.currentTarget.dataset.story === "home") {
            this.setState({history: ["home"]})
        }
        
        this.setState({ activeStory: e.currentTarget.dataset.story, activePanel: e.currentTarget.dataset.story})
        window.scrollTo(0, 0)
    }

    render() {
        return(
            <ConfigProvider isWebView={true}>
            <Epic activeStory={this.state.activeStory} tabbar={
            this.state.activePanel !== 'recipe_info' && this.state.activePanel !== 'recipe_suggest' &&
                <Tabbar>
                  <TabbarItem
                    onClick={this.onStoryChange}
                    selected={this.state.activeStory === 'home'}
                    data-story="home"
                    text="Рецепты"
                  ><Icon28Menu /></TabbarItem>
                  <TabbarItem
                    onClick={this.onStoryChange}
                    selected={this.state.activeStory === 'add'}
                    data-story="add"
                    text="Предложить"
                  ><Icon28AddOutline /></TabbarItem>
                  <TabbarItem
                    onClick={this.onStoryChange}
                    selected={this.state.activeStory === 'info'}
                    data-story="info"
                    text="О нас"
                  ><Icon28InfoOutline/></TabbarItem>
                </Tabbar>
              }>
                <View 
                id="home" 
                activeStory={this.state.activeStory} 
                activePanel={this.state.activePanel}
                onSwipeBack={this.goBack}
                popout={this.state.popout}
                history={this.state.history}>
                  <Home id="home" this={this}/>
                  <Category id="category" this={this}/>
                  <CategoryADV id="categoryavd" this={this}/>
                  <Recipes_info id="recipe_info" this={this}/>
                </View>
                <View 
                id="add" 
                activeStory={this.state.activeStory} 
                activePanel={this.state.activePanel} 
                onSwipeBack={this.goBack}
                popout={this.state.popout}
                history={this.state.history}>
                  <Add id="add" this={this}/>
                  <Recipe_Suggest id="recipe_suggest" this={this}/>
                  <Add_Recipe id='add_recipe' this={this}/>
                </View>
                <View id="info" activeStory={this.state.activeStory} activePanel="info">
                  <More id="info" this={this}/>
                </View>
              </Epic>
              </ConfigProvider>
        );
    }
}

export default App;