import React from 'react';
import {Button, Header, Input, FormLayoutGroup, FormLayout, Textarea, IOS, Panel, platform, PanelHeader} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
const Home = ({id, this: props})=> (
    <Panel id={id}>
        <PanelHeader left={<Header onClick={ () => props.goBack()}>{platform() === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</Header>}>{props.state.add_name === "" ? "Булочка" : props.state.add_name}</PanelHeader>
        <FormLayout>
                <FormLayoutGroup top="Название">
                    <Input maxLength="30" placeholder="Булочка" value={props.state.add_name} onChange={props.onChangeRezeptAddName}/>
                </FormLayoutGroup>
                <FormLayoutGroup top="Категория">
                    <Input maxLength="50" placeholder="Выпечка" value={props.state.add_category} onChange={props.onChangeRezeptAddCategory}/>
                </FormLayoutGroup>
                <FormLayoutGroup top="Ваш псевдоним">
                    <Input maxLength="30" placeholder="Герцелиус" value={props.state.add_author_name} onChange={props.onChangeRezeptAddAuthorName}/>
                </FormLayoutGroup>
                <FormLayoutGroup top="Время приготовления">
                    <Input maxLength="30" placeholder="30 минут" value={props.state.add_time_for_preparing} onChange={props.onChangeRezeptAddTime}/>
                </FormLayoutGroup>
                <FormLayoutGroup maxLength="10000" top="Ингредиенты">
                    <Textarea placeholder="• Молоко 1л и т.д." value={props.state.add_ingredients} onChange={props.onChangeRezeptAddIngred}/>
                </FormLayoutGroup>
                <FormLayoutGroup maxLength="10000" top="Приготовление">
                    <Textarea placeholder="1) Включить огонь. 2) Поставить на плиту." value={props.state.add_recipe} onChange={props.onChangeRezeptAddRecipe}/>
                </FormLayoutGroup>
                <Button size="xl" level="secondary" onClick={props.SuggestRecipe}>Отправить</Button>
                </FormLayout>
    </Panel>
);

export default Home;