import React from 'react';
import {Panel, PanelHeader, Header, platform, IOS, Group, Button, List, Cell, Link, Div} from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon24NewsFeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';
import Icon24Reply from '@vkontakte/icons/dist/24/reply';
const recipes_info = ({id, this: props})=> (
    <Panel id={id}>
        <PanelHeader left={<Header onClick={ () => props.goBack()}>{platform() === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</Header>}>{props.state.recipes_info_full['name']}</PanelHeader>
        <Group title="Информация">
            <List>
            {props.state.recipes_info_full['information']['rating'] === 0 ? null : <Cell style={{userSelect: "none"}} before={<Icon24Favorite fill="#ffa500"/>} indicator={props.state.recipes_info_full['information']['rating']}>Рейтинг</Cell>}
            <Cell style={{userSelect: "none"}} before={<Icon24NewsFeed fill="#4bb34b" />} indicator={<Link component = "a" target="_blank" href={"https://vk.com/id" +props.state.recipes_info_full['information']['author_id']}>{props.state.recipes_info_full['information']['author_name']}</Link>}>Автор</Cell>
            {props.state.recipes_info_full['information']['time_for_preparing'] === "0" ? null : <Cell style={{userSelect: "none"}} before={<Icon24Recent fill="#4bb34b" />} indicator={props.state.recipes_info_full['information']['time_for_preparing']}>Время приготовления</Cell>}
            </List>
        </Group>
        <Group title="Ингредиенты:">
            <Div>
            <div style={{whiteSpace: "pre-wrap", userSelect: "none"}}>{props.state.recipes_info_full['information']['ingredients']}</div>
            </Div>
        </Group>
        <Group title="Рецепт:">
            <Div>
                <div style={{whiteSpace: "pre-wrap", userSelect: "none"}}>{props.state.recipes_info_full['information']['recipe']}</div>
            </Div>
        </Group>
        <Div>
        <Button before={<Icon24Reply/>} size="xl" level="secondary" onClick={() => props.Share()}>Поделиться рецептом</Button>
        </Div>
    </Panel>
);
export default recipes_info;