import React from 'react';
import {Panel, PanelHeader, Header, platform, IOS, Group, List, Cell} from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
const categoryadv = ({id, this: props})=> (
    <Panel id={id}>
        <PanelHeader left={<Header onClick={ () => props.goBack()}>{platform() === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</Header>}>{props.state.category_selected}</PanelHeader>
        <Group>
            <List>
                {props.recipes.length > 0 &&
                    props.recipes.map(function(result, i) {
                        if(result['category'] === props.state.category_selected) {
                            return <Cell expandable key={i} onClick={() => props.GetRecipesInfo(result['id'])}>{result['name']}</Cell>
                        }
                    })
                }
            </List>
        </Group>
    </Panel>
);

export default categoryadv;