import React from 'react';
import {Panel, PanelHeader, Div, CellButton, Avatar, Cell, List, Group, Switch} from '@vkontakte/vkui';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon28Profile from '@vkontakte/icons/dist/28/profile';

const category = ({id, this: props})=> (
  <Panel id={id}>
        <PanelHeader>
            О нас
            </PanelHeader>

<Group>
  <Div style={{marginTop: "-20px", marginBottom: "-20px"}}>
  <CellButton style={{userSelect: "none" }} before={<Icon24Flash />} component="a" target="_blank"  href="https://vk.com/xelenestudio">Группа ВКонтакте</CellButton>
  </Div>
    </Group>
<Group>
        <List>
          <Div style={{marginTop: "-20px", marginBottom: "-20px"}}>
          <Cell
            before={<Avatar style={{ background: 'var(--accent)', userSelect: "none" }} size={28}><Icon24Info fill="var(--white)" /></Avatar>}
            description="Версия 20.0"
          >
            Информация
          </Cell>
          </Div>
        </List>
      </Group>
      <Group title="Настройки">
            <Div style={{marginTop: "-20px", marginBottom: "-20px"}}>
            {props.state.sheme === "space_gray" ? 
            <Cell before={<Icon28Profile/>} asideContent={<Switch defaultChecked onChange={() => props.UpdateTheme()}/>}>Тёмный режим</Cell>
            : null
            }
            {props.state.sheme === "client_dark" ? 
            <Cell before={<Icon28Profile/>} asideContent={<Switch defaultChecked onChange={() => props.UpdateTheme()}/>}>Тёмный режим</Cell> 
            : null
            }
            {props.state.sheme === "client_light" ? 
            <Cell before={<Icon28Profile/>} asideContent={<Switch onChange={() => props.UpdateTheme()}/>}>Тёмный режим</Cell>
            : null
            }
            {props.state.sheme === "bright_light" ? 
            <Cell before={<Icon28Profile/>} asideContent={<Switch onChange={() => props.UpdateTheme()}/>}>Тёмный режим</Cell> 
            : null
            }
            {props.state.sheme === "" ? 
            <Cell before={<Icon28Profile/>} asideContent={<Switch disabled onChange={() => props.UpdateTheme()}/>}>Тёмный режим</Cell> 
            : null
            }
            {console.log(props.state.sheme)}
            </Div>
        </Group>
    </Panel>
);

export default category;