import React from 'react';
import {Panel, PanelHeader, Header, platform, IOS, Group, List, Cell, Search} from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';

const category = ({id, this: props})=> (
    <Panel id={id}>
        <PanelHeader left={<Header onClick={ () => props.goBack()}>{platform() === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</Header>}>Категории</PanelHeader>
        <Search maxLength="25" value={props.state.search_category}  onChange={(e) => props.onChangeCategory(e.currentTarget.value)}/>
        <Group>
            <List>
                {props.category.length > 0 &&
                    props.category.map(function(result, i) {
                        return <Cell expandable key={i} onClick={() => {props.setState({category_selected: result}); props.setState({activePanel: 'categoryavd', history: [...props.state.history, 'categoryavd']})}}>{result}</Cell>
                    })
                }
                {props.category.length === 0 &&
                    <Cell style={{userSelect: "none"}}>По вашему запросу ничего не найдено</Cell>
                }
            </List>
        </Group>
    </Panel>
);


export default category;